define('ember-htmlbars/hooks/bind-local', ['exports', 'ember-metal/streams/stream', 'ember-metal/streams/simple'], function (exports, Stream, SimpleStream) {

  'use strict';



  exports['default'] = bindLocal;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  function bindLocal(env, scope, key, value) {
    var existing = scope.locals[key];

    if (existing) {
      existing.setSource(value);
      existing.notify();
    } else {
      var newValue = Stream['default'].wrap(value, SimpleStream['default']);
      scope.locals[key] = newValue;
    }
  }

});