define('ember-views/mixins/instrumentation_support', ['exports', 'ember-metal/mixin', 'ember-metal/computed', 'ember-metal/property_get'], function (exports, mixin, computed, property_get) {

  'use strict';

  var InstrumentationSupport = mixin.Mixin.create({
    /**
      Used to identify this view during debugging
       @property instrumentDisplay
      @type String
    */
    instrumentDisplay: computed.computed(function () {
      if (this.helperName) {
        return "{{" + this.helperName + "}}";
      }
    }),

    instrumentName: "view",

    instrumentDetails: function (hash) {
      hash.template = property_get.get(this, "templateName");
      this._super(hash);
    }
  });

  exports['default'] = InstrumentationSupport;

});