define('ember-htmlbars/helpers', ['exports', 'ember-metal/platform/create'], function (exports, o_create) {

  'use strict';

  exports.registerHelper = registerHelper;

  /**
  @module ember
  @submodule ember-htmlbars
  */

  /**
    @private
    @method _registerHelper
    @for Ember.HTMLBars
    @param {String} name
    @param {Object|Function} helperFunc the helper function to add
  */
  var helpers = o_create['default'](null);
  function registerHelper(name, helperFunc) {
    helpers[name] = helperFunc;
  }

  exports['default'] = helpers;

});