define('ember-htmlbars/keywords/partial', ['exports', 'ember-views/system/lookup_partial', 'htmlbars-runtime'], function (exports, lookupPartial, htmlbars_runtime) {

  'use strict';



  exports['default'] = partialKeyword;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  function partialKeyword(morph, env, scope, params, hash, template, inverse, visitor) {
    var found = lookupPartial['default'](env, env.hooks.getValue(params[0])).raw;

    htmlbars_runtime.internal.hostBlock(morph, env, scope, found, null, null, visitor, function (options) {
      options.templates.template.yield();
    });

    return true;
  }

});