define('ember-views/mixins/template_rendering_support', ['exports', 'ember-metal/mixin'], function (exports, mixin) {

  'use strict';

  var _renderView;

  var TemplateRenderingSupport = mixin.Mixin.create({
    /**
      Called on your view when it should push strings of HTML into a
      `Ember.RenderBuffer`. Most users will want to override the `template`
      or `templateName` properties instead of this method.
       By default, `Ember.View` will look for a function in the `template`
      property and invoke it with the value of `context`. The value of
      `context` will be the view's controller unless you override it.
       @method render
      @param {Ember.RenderBuffer} buffer The render buffer
    */
    render: function (buffer) {},

    renderTemplate: function () {
      if (_renderView === undefined) {
        _renderView = require("ember-htmlbars/system/render-view");
      }

      return _renderView.renderHTMLBarsTemplate.apply(this, arguments);
    }
  });

  exports['default'] = TemplateRenderingSupport;

  // TODO: Legacy string render function support

});