define('ember-htmlbars/hooks/link-render-node', ['exports', 'ember-htmlbars/utils/subscribe', 'ember-views/streams/should_display', 'ember-metal/streams/utils'], function (exports, subscribe, shouldDisplay, utils) {

  'use strict';



  exports['default'] = linkRenderNode;

  /**
  @module ember
  @submodule ember-htmlbars
  */

  function linkRenderNode(renderNode, scope, path, params, hash) {
    if (renderNode.state.unsubscribers) {
      return true;
    }

    switch (path) {
      case "unbound":
        return true;
      case "if":
        params[0] = shouldDisplay['default'](params[0]);break;
      case "each":
        params[0] = eachParam(params[0]);break;
    }

    if (params.length) {
      for (var i = 0; i < params.length; i++) {
        subscribe['default'](renderNode, scope, params[i]);
      }
    }

    if (hash) {
      for (var key in hash) {
        subscribe['default'](renderNode, scope, hash[key]);
      }
    }

    // The params and hash can be reused; they don't need to be
    // recomputed on subsequent re-renders because they are
    // streams.
    return true;
  }

  function eachParam(list) {
    var listChange = list.getKey("[]");

    var stream = utils.chain(list, function () {
      utils.read(listChange);
      return utils.read(list);
    });

    stream.addDependency(listChange);
    return stream;
  }

});