define('ember-htmlbars/hooks/bind-scope', ['exports', 'ember-htmlbars/utils/update-scope'], function (exports, updateScope) {

  'use strict';



  exports['default'] = bindScope;
  function bindScope(env, scope) {
    if (scope.attrs || scope.view) {
      updateScope['default'](scope.locals, "view", scope.view, null);
    }
  }

});