define('ember-runtime/system/lazy_load', ['exports', 'ember-metal/core', 'ember-metal/array', 'ember-runtime/system/native_array'], function (exports, Ember, array) {

  'use strict';

  exports.onLoad = onLoad;
  exports.runLoadHooks = runLoadHooks;

  /**
    Detects when a specific package of Ember (e.g. 'Ember.Handlebars')
    has fully loaded and is available for extension.

    The provided `callback` will be called with the `name` passed
    resolved from a string into the object:

    ``` javascript
    Ember.onLoad('Ember.Handlebars' function(hbars) {
      hbars.registerHelper(...);
    });
    ```

    @method onLoad
    @for Ember
    @param name {String} name of hook
    @param callback {Function} callback to be called
  */
  var loadHooks = Ember['default'].ENV.EMBER_LOAD_HOOKS || {};
  var loaded = {};
  function onLoad(name, callback) {
    var object;

    loadHooks[name] = loadHooks[name] || Ember['default'].A();
    loadHooks[name].pushObject(callback);

    if (object = loaded[name]) {
      callback(object);
    }
  }

  function runLoadHooks(name, object) {
    loaded[name] = object;

    if (typeof window === "object" && typeof window.dispatchEvent === "function" && typeof CustomEvent === "function") {
      var event = new CustomEvent(name, { detail: object, name: name });
      window.dispatchEvent(event);
    }

    if (loadHooks[name]) {
      array.forEach.call(loadHooks[name], function (callback) {
        callback(object);
      });
    }
  }

});