define('ember-htmlbars/keywords/debugger', ['exports', 'ember-metal/logger'], function (exports, Logger) {

  'use strict';



  /**
    Execute the `debugger` statement in the current template's context.
    ```handlebars
    {{debugger}}
    ```
    When using the debugger helper you will have access to a `get` function. This
    function retrieves values available in the context of the template.
    For example, if you're wondering why a value `{{foo}}` isn't rendering as
    expected within a template, you could place a `{{debugger}}` statement and,
    when the `debugger;` breakpoint is hit, you can attempt to retrieve this value:
    ```
    > get('foo')
    ```
    `get` is also aware of keywords. So in this situation
    ```handlebars
    {{#each items as |item|}}
      {{debugger}}
    {{/each}}
    ```
    you'll be able to get values from the current item:
    ```
    > get('item.name')
    ```
    You can also access the context of the view to make sure it is the object that
    you expect:
    ```
    > context
    ```
    @method debugger
    @for Ember.Handlebars.helpers
    @param {String} property
  */
  exports['default'] = debuggerKeyword;
  /*jshint debug:true*/

  /**
  @module ember
  @submodule ember-htmlbars
  */
  function debuggerKeyword(morph, env, scope) {

    /* jshint unused: false */
    var view = scope.locals.view;

    /* jshint unused: false */
    var context = scope.self;

    /* jshint unused: false */
    function get(path) {
      return env.hooks.getValue(env.hooks.get(path));
    }

    Logger['default'].info("Use `view`, `context`, and `get(<path>)` to debug this template.");

    debugger;

    return true;
  }

});