define('ember-htmlbars/helpers/each', ['exports', 'ember-metal/property_get', 'ember-metal/enumerable_utils'], function (exports, property_get, enumerable_utils) {

  'use strict';



  exports['default'] = eachHelper;
  function eachHelper(params, hash, blocks) {
    var list = params[0];
    var keyPath = hash.key;

    // TODO: Correct falsy semantics
    if (!list) {
      if (blocks.inverse.yield) {
        blocks.inverse.yield();
      }
      return;
    }

    enumerable_utils.forEach(list, function (item, i) {
      var key = keyPath ? property_get.get(item, keyPath) : String(i);
      blocks.template.yieldItem(key, [item, i]);
    });
  }

});