define('ember-htmlbars/utils/update-scope', ['exports', 'ember-metal/streams/simple', 'ember-htmlbars/utils/subscribe'], function (exports, SimpleStream, subscribe) {

  'use strict';



  exports['default'] = updateScope;
  function updateScope(scope, key, newValue, renderNode) {
    var existing = scope[key];

    if (existing) {
      existing.setSource(newValue);
    } else {
      var stream = new SimpleStream['default'](newValue);
      if (renderNode) {
        subscribe['default'](renderNode, scope, stream);
      }
      scope[key] = stream;
    }
  }

});