define('ember-htmlbars/hooks/classify', ['exports', 'ember-htmlbars/utils/is-component'], function (exports, isComponent) {

  'use strict';



  exports['default'] = classify;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  function classify(env, scope, path) {
    if (isComponent['default'](env, scope, path)) {
      return "component";
    }

    return null;
  }

});