define('ember-htmlbars/hooks/bind-self', ['exports', 'ember-metal/property_get', 'ember-htmlbars/utils/update-scope'], function (exports, property_get, updateScope) {

  'use strict';



  exports['default'] = bindSelf;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  function bindSelf(env, scope, self) {
    Ember.assert("BUG: scope.attrs and self.isView should not both be true", !(scope.attrs && self.isView));

    if (self.isView) {
      scope.view = self;
      updateScope['default'](scope.locals, "view", self, null);
      updateScope['default'](scope, "self", property_get.get(self, "context"), null);
      return;
    }

    updateScope['default'](scope, "self", self, null);
  }

});