define('ember-views/mixins/view_stream_support', ['exports', 'ember-metal/mixin', 'ember-metal/streams/simple'], function (exports, mixin, SimpleStream) {

  'use strict';

  var ViewStreamSupport = mixin.Mixin.create({
    init: function () {
      this._scopeStream = new SimpleStream['default'](this).getKey("context");
      this._super.apply(this, arguments);
    },

    getStream: function (path) {
      var stream = this._scopeStream.get(path);
      stream._label = path;

      return stream;
    }
  });

  exports['default'] = ViewStreamSupport;

});